.anim-appear {
    opacity: 0.01;
}

.anim-appear.anim-appear-active {
    opacity: 1;
    -webkit-transition: opacity 1.5s ease-in;
    -o-transition: opacity 1.5s ease-in;
    transition: opacity 1.5s ease-in;
}

.example-enter {
    opacity: 0.01;
}

.anim-enter.anim-enter-active {
    opacity: 1;
    -webkit-transition: opacity 1.5ms ease-in;
    -o-transition: opacity 1.5ms ease-in;
    transition: opacity 1.5ms ease-in;
}

.anim-leave {
    opacity: 1;
}

.anim-leave.anim-leave-active {
    opacity: 0.01;
    -webkit-transition: opacity 1.5ms ease-in;
    -o-transition: opacity 1.5ms ease-in;
    transition: opacity 1.5ms ease-in;
}

.item-list {
    margin-left: 20px;
}

.task-app {
    z-index: 999;
    position: absolute;
    left: 27%;
    top: 20%;
}

body {
    margin: 0;
}


